import React from 'react'
import Link from 'gatsby-link'

import logo from '../assets/images/Netspective-Lectio.png'

const Header = props => (
  <header id="header" className="alt">
    <Link to="/">
      <span className="logo">
        <img src={logo} alt="" />
      </span>
    </Link>
    <h1>Lectio</h1>
    {/* <p>
      Just another free, fully responsive site template<br />
      design by <a href="https://html5up.net">HTML5 UP</a>.
    </p> */}
  </header>
)

export default Header



